import * as React from 'react';
import { IExperienceCardProps } from './ExperienceCard';
import { ExperinceCardSliderComponent } from './ExperienceCardSlider'
import { useMediaQuery } from 'react-responsive'

const erzasoftWork: IExperienceCardProps = {
    Description: "Programovn a kdovn webovch strnek za pomoci jazyka C# a technologi ASP.NET MVC 5.",
    DescriptionHtml:
        <p>
            V Erasoftu jsme se vnovi vvoji a kdovn webovch strnek za pomoci jazyka C# a technologi ASP.NET MVC 5.
            Mimo tyto technologie jsem pouval tak HTML5, Javascript, Sass pro kdovn strnek.
        </p>,
    From: "05/2013",
    To: "03/2014",
    Name: "Erzasoft s.r.o.",
    Technologies: ["EntityFramework", "MS SQL", "Telerik (UI for ASP.NET MVC)", "Javascript", "HTML5", "CSS3", "Sass", "TFS", "Adobe Photoshop"],
    Position: "Software developer"
};
const trsWork: IExperienceCardProps = {
    Description: "Programovn Windows Forms aplikac za pomoci jazyka C#",
    DescriptionHtml: <p>Programovn Windows Forms aplikac za pomoci jazyka C#</p>,
    From: "03/2014",
    To: "12/2014",
    Name: "TRS s.r.o.",
    Technologies: ["EntityFramework", "MS SQL", "Telerik (UI for ASP.NET MVC)", "Javascript", "HTML5", "CSS3", "Sass", "TFS", "Adobe Photoshop"],
    Position: "Software developer"
};
const proficreditSoftwareDeveloperWork: IExperienceCardProps = {
    // Description: "Programovn .NET aplikac veho druhu, ale primrn jde o aplikace vytvoen pomoc technologie WPF a ASP.NET MVC 5 a postaven nad MSSQL.",
    Description: "Programovn rznch typ .NET aplikac. Primrn se jednalo o aplikace vytvoen pomoc technologie WPF a ASP.NET MVC 5 nebo Core. Apliakce pod sebou mly postavnou MSSQL.",
    DescriptionHtml:
        <div>
            <p>
                Jako software developer jsem se podlel na vvoji rznch typ aplikac na .NET platform.
                Primrn se jednalo o aplikace vytvoen pomoc technologie ASP NET MVC 5, WPF a WCF. K verzovn zdrojovho kdu jsem vyuvali TFS.
                Aplikace spolupracovaly s MSSQL databz.
            </p>
        </div>,
    From: "01/2015",
    To: "01/2017",
    Name: "PROFI CREDIT Czech, a. s.",
    Technologies: ["JIRA", "ASP.NET Core", "Web API", "Javascript", "HTML5", "CSS3", "Team foundation server", "WCF", "WinService", "PHP"],
    Position: "Software developer"
};
const proficreditDevelopmentLeaderWork: IExperienceCardProps = {
    Description: "Jako development leader jsem se stal lenem malho tmu. Moj rol je krom standartnho vvoje, tak v rmci monost rozhodovat o architektue aplikace a pouit technologii. Soust m prce se tak stalo code review, kter se snam dlat pro programtory mho tmu.",
    DescriptionHtml:
        <div>
            <p>
                S novou pozic pila nov zodpovdnost. Po rozen IT oddlen a rozdlen na mal teamy jsem se stal development leaderem jednoho z nich.
                I s novou pozic jsem se primrn vnoval programovn projekt, kter jsem v teamu mli na starost. Vyvjeli jsme primrn ASP NET Core MVC, ASP NET Core WebApi a WPF.
                Mimo to jsem se zaal vnovat code review pro programtory v naem teamu a jako developement leader jsem ml monost rozhodovat o architektue aplikace a pouit technologii.
                Tak jsem se vce zapojoval co vvoje naich core projekt a diskuze o tchto projektech. V naem teamu probhal vvoj pln agiln.
            </p>
        </div>,
    From: "01/2017",
    To: "souasnost",
    Name: "PROFI CREDIT Czech, a. s.",
    Technologies: ["JIRA", "ASP.NET Core", "Web API", "Javascript", "HTML5", "CSS3", "Team foundation server", "WCF", "WinService", "PHP"],
    Position: "Developement leader"
};
const highSchool: IExperienceCardProps = {
    Description: undefined,
    From: "05/2006",
    To: "05/2010",
    Name: "Stedn prmyslov kola elektrotechnick",
    Technologies: [],
    Position: undefined
};
const university: IExperienceCardProps = {
    Description: undefined,
    From: "05/2010",
    To: "05/2014",
    Name: "Univerzita Pardubice - Fakulta elektrotechniky a informatiky",
    Technologies: [],
    Position: undefined
};

const WorkAndSchoolComponent = (props: any) => {
    const isMobile = useMediaQuery({ query: '(max-width: 640px)' })
    const isTablet = useMediaQuery({ query: '(min-width: 641px) and (max-width: 1023px)' })
    const isDesktop = useMediaQuery({ query: '(min-width: 1024px)' })

    return (
        <div className="text-white px-12">
            <h1 className="text-left text-5xl mt-6">Praxe a vzdln</h1>
            <div className="flex flex-col">
                <div className="w-full">
                    <h2 className="text-2xl">Zkuenosti</h2>
                    <div className="flex mt-4">
                        {isDesktop && <ExperinceCardSliderComponent maxItemsCount={3} cards={[erzasoftWork, trsWork, proficreditSoftwareDeveloperWork, proficreditDevelopmentLeaderWork]} />}
                        {isTablet && <ExperinceCardSliderComponent maxItemsCount={2} cards={[erzasoftWork, trsWork, proficreditSoftwareDeveloperWork, proficreditDevelopmentLeaderWork]} />}
                        {isMobile && <ExperinceCardSliderComponent maxItemsCount={1} cards={[erzasoftWork, trsWork, proficreditSoftwareDeveloperWork, proficreditDevelopmentLeaderWork]} />}
                    </div>
                </div>
                <div className="w-full mt-12">
                    <h2 className="text-2xl">Vzdln</h2>
                    <div className="flex mt-4">
                        {isDesktop && <ExperinceCardSliderComponent maxItemsCount={3} cards={[highSchool, university]} />}
                        {isTablet && <ExperinceCardSliderComponent maxItemsCount={2} cards={[highSchool, university]} />}
                        {isMobile && <ExperinceCardSliderComponent maxItemsCount={1} cards={[highSchool, university]} />}
                    </div>
                </div>
            </div>
        </div>
    )
}

export default WorkAndSchoolComponent